<template>
	<el-row :gutter="20" class="container">
		  <el-col :span="14">
			  	<div class="login-icon">
			  		<img  src="../assets/imgs/login.jpg">
			  	</div>
		  </el-col>
		  <el-col :span="10">
			  	<el-main>
			    	<el-form :model="ruleForm2" status-icon :rules="rules2" ref="ruleForm2" label-width="100px" class="demo-ruleForm">
					  <el-form-item label="用户名称" prop="account">
					   		<el-input type="text" v-model="ruleForm2.account" auto-complete="off"></el-input>
					  </el-form-item>
					  <el-form-item label="密码" prop="pass">
					     <el-input type="password" v-model="ruleForm2.pass" auto-complete="off"></el-input>
					  </el-form-item>
					  <el-form-item>
					    <el-button type="primary" @click="submitForm('ruleForm2')" class="btn0">登&emsp;录</el-button>
					  </el-form-item>
					   <el-form-item>
					   		<span class="fl">没有账号？</span>
					   		<router-link to="/register/stepFth" class="fr a-link">注册</router-link>
					  </el-form-item>
					</el-form>
				</el-main>
		  </el-col>
		</el-row>
</template>
<script>
import {Base64} from '@/assets/js/base64.js'

export default {
  name: 'App',
   data() {
      var checkAccount = (rule, value, callback) => {
      	var flag;//标识  （ phone 手机、 username 用户名）
        if (!value.match(/\S/)) {
          return callback(new Error('用户名不能为空'));
        }

        flag = (value.match(/\d{4,}/))?"phone" :"username";

        if(flag == "phone") {
        	if(!value.match(/^((\+86\-?)|(\(\+86\)))?1[34578][0-9]{9}$/)) {
	        	return callback(new Error('请输入正确的号码'));
	        }
        } else {
        	if(!value.match(/^[\u4e00-\u9fa5a-zA-Z0-9\-\_\\()]{4,20}$/)) {
	        	return callback(new Error('4-20个字符，支持文字、英文（区分大小写），支持数字“_” “-”、()的组合'));
	        }
        }
		return callback();
      };

      var checkPass = (rule, value, callback) => {
      	if (!value.match(/\S/)) {
          return callback(new Error('密码不能为空'));
        }
        if(!value.match(/^[^\u4e00-\u9fa5\s]{6,20}$/)) {
        	return callback(new Error('6-20个字符，由数字、英文字母（区分大小写）或符号两种以及以上的组合，不允许有空格'));
        }
				return callback();
      };

      return {
        ruleForm2: {
          pass: '',
          account: ''
        },
        rules2: {
          account: [
            { validator: checkAccount, trigger: 'blur' }
          ],
          pass: [
          	 { validator: checkPass, trigger: 'blur' }
          ]
        }
      };
    },
  	methods: {
    submitForm(formName) {
			 //	state 转成 base64
				let stateStr = JSON.stringify({
						context_url: '/',
						app_session_id: '',
						device_info: {
							deviceType: 'PC', 
						},
						others: {} 		
				});

				let device_info = Base64.toBase64(JSON.stringify({
															deviceType: 'PC', 
															osType: 'windows', 
															agentType: 'Browser', 
															hardwareUid: {
																	PC:{
																			disk_id: '',
																			mac_addresses:'' 
																	}
															}        
													}));

			 	let state = Base64.toBase64(stateStr);
        this.$refs[formName].validate((valid) => {
          if (valid) {
						//假设已经成功了
						let params = this.$route.params;
						params.account = this.ruleForm2.account;
						params.pass = this.ruleForm2.pass;
						this.$router.push({name: 'home', params: {data: 'http://localhost' } }); 
						this.$store.dispatch('login', params).then(response => {
								alert('登录成功');
								//this.$router.push({name: 'home', params: {data: response.data.data } }); 
						}).catch(err => {
								alert('添加失败：' + err.data.data);
						});
            this.$refs.ruleForm2.resetFields();
          } else {
            console.log('error submit!!');
            return false;
          }
        });
      },
      resetForm(formName) {}
    },
    created() {},
		mounted(){},
		updated() {}
}
</script>



